<template>
	<div id="deliveryer-location-com">
		<div id="map-container"></div>
	</div>
</template>

<script>
	import {Amap} from '@/config/AMap'
	import {getData} from "@/controller/funcCommon"
	export default {
		props: {
			deliveryer: {
				type: Array,
				default: () => {return [];}
			},
			id: 0,
		},
		data() {
			return {

			}
		},
		methods: {
			onLoad() {
				var that = this;
				Amap().then(function(AMap){
					var map = new AMap.Map('map-container', {
						resizeEnable: true,
						zoom: 12,
					});

					var markers = [];
					var deliveryer = that.deliveryer;
					var markerContent = '';
					for (var i = 0; i < deliveryer.length; i++) {
						// 点标记显示内容，HTML要素字符串
						markerContent = '' +
							'<div class="deliveryer-item">' +
							'<div class="deliveryer-info flex-lr" id="deliveryer-info">'+
							'<span class="deliveryer-name">' + deliveryer[i]['title'] + '</span>'+
							'<span class="delivery-times-info">'+
							'<span class="wait-pickup">' + deliveryer[i]['wait_pickup'] + '</span>'+
							' / ' +
							'<span class="wait-delivery">' + deliveryer[i]['wait_delivery'] + '</span>'+
							'</span>'+
							'<span class="icon icon-attention hide"></span>'+
							'</div>' +
							'<img class="location-dot" src="static/img/location_dot.png">' +
							'</div>';
						// 创建点实例
						var marker = new AMap.Marker({
							position: new AMap.LngLat(deliveryer[i]['location_y'], deliveryer[i]['location_x']),
							content: markerContent,
							extData:{
								id: i + 1
							}
						});
						markers.push(marker);
					}
					map.add(markers);
					map.setFitView();
				});
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#deliveryer-location-com{
		width: 100%;
		height: 100%;
	}
	#deliveryer-location-com #map-container{
		width: 100%;
		height: 100%;
	}
	#deliveryer-location-com #map-container .deliveryer-item{
		border: 1px solid #999;
		border-radius: 2px;
		position: relative;
		z-index: 19;
		background: #eee;
		color: #999;
		font-size: 14px;
		text-align: center;
		line-height: 30px;

	}
	#deliveryer-location-com #map-container .deliveryer-item:before, #deliveryer-location-com #map-container .deliveryer-item:after{
		content: "";
		display: block;
		border-width: 10px;
		position: absolute;
		bottom: -20px;
		left: 30px;
		border-style: solid dashed dashed;
		border-color: #999 transparent transparent;
		font-size: 0;
		line-height: 0;
		z-index: 10;
	}
	#deliveryer-location-com #map-container .deliveryer-item:before{
		bottom: -20px;
		border-color: #999 transparent transparent;
	}
	#deliveryer-location-com #map-container .deliveryer-item:after{
		bottom: -19px;
		border-color: #eee transparent transparent;
	}
	#deliveryer-location-com #map-container .deliveryer-item .deliveryer-name{
		display: block;
		font-size: 14px;
		width: 60px;
		height: 30px;
	}
	#deliveryer-location-com #map-container .deliveryer-item .delivery-times-info{
		width: 40px;
		background-color: #fff;
		height: 30px;
		color: #333;
	}
	#deliveryer-location-com #map-container .deliveryer-item .delivery-times-info .wait-delivery{
		color: #f90;
	}
	#deliveryer-location-com #map-container .deliveryer-item .icon{
		width: 30px;
		height: 30px;
		font-size: 16px;
	}
	#deliveryer-location-com #map-container .deliveryer-item .location-dot{
		position: absolute;
		left: 30px;
		bottom: -18px;
		width: 21px;
		height: 15px;
		z-index: 9;
	}
</style>


