import util from '@/config/util.js'
import router from '@/router'
import {setData} from "@/controller/funcCommon.js"
function getOrderList(params) {
	if(params.force) {
		params.vue.orders = {
			page: 1,
			psize: 15,
			loading: true,
			finished: false,
			empty: false,
			data:[]
		};
	}
	if(params.vue.orders.finished) {
		return false;
	}
	params.vue.util.request({
		url: 'plateform/order/takeout/list',
		data: {
			page: params.vue.orders.page,
			psize: params.vue.orders.psize,
			filter: params.data.filter,
			menufooter: params.data.menufooter,
		}
	}).then((res) => {
		let result = res.data.message;
		params.vue.showPreLoading = false;
		if(result.errno) {
			params.vue.util.$toast(result.message);
			return false;
		}
		var order = result.message.order;
		params.vue.orders.data = [...params.vue.orders.data,...order];
		if(order.length < params.vue.orders.psize ) {
			params.vue.orders.finished = true;
			if(!params.vue.orders.data.length) {
				params.vue.orders.empty = true;
			}
		}
		params.vue.orders.page++;
		params.vue.orders.loading = false;
		params.vue.isRefresh = false;
		params.vue.menufooter = window.menufooter;
	})
};

function orderStatusChangeTips(type) {
	var types = {
		notify_clerk_handle: {
			tip: '确定通知商户接单吗',
			success_status: 0
		},
		handle: {
			tip: '确定接单吗',
			success_status: 2
		},
		notify_deliveryer_collect: {
			tip: '确定通知配送员抢单吗',
			success_status: 3
		},
		re_notify_deliveryer_collect: {
			tip: '该订单配送员已经接单,确定将订单重新放入待抢订单列表吗',
			success_status: 3
		},
		end: {
			tip: '确定完成订单吗',
			success_status: 5
		},
		cancel: {
			tip: '确定取消订单吗',
			success_status: 6
		},
		cancel_refund: {
			tip: '确定取消订单并退款吗',
			success_status: 6
		},
		refund_handle: {
			tip: '确定发起退款吗',
			success_status: 2
		},
		refund_status: {
			tip: '确定设置为已退款吗',
			success_status: 3
		},
		refund_query: '',
	}
	return types[type];
};

function changeOrderStatus(options) {
	var order = options.order;
	var type = options.type;
	var typeInfo = orderStatusChangeTips(type);
	var tip = typeInfo.tip;
	var success_status = typeInfo.success_status;
	if(type == 'cancel' || type == 'cancel_refund') {
		router.push(util.getUrl({path: '/pages/order/reason', query: {order_id: order.id, tip: tip, order_status: order.status}}));
	} else if(type == 'refund_handle') {
		var params = {
			url: 'plateform/order/takeout/refund_handle',
			data: {
				id: order.id,
			},
			confirm: tip,
			success: function(message) {
				util.$toast(message);
				if(options.from == 'detail') {
					options.vue.order.refund_status = success_status;
				} else {
					options.vue.orders.data[options.index].refund_status = success_status;
					if(options.vue.filter.items.status != 6 && options.from != 'search') {
						options.vue.orders.data.splice(options.index, 1);
					}
				}
			}
		};
		util.jspost(params);
	} else if(type == 'refund_status') {
		var params = {
			url: 'plateform/order/takeout/refund_status',
			data: {
				id: order.id,
			},
			confirm: tip,
			success: function(message) {
				util.$toast(message);
				if(options.from == 'detail') {
					options.vue.order.refund_status = success_status;
				} else {
					options.vue.orders.data[options.index].refund_status = success_status;
				}
			}
		};
		util.jspost(params);
	} else if(type == 'refund_query') {
		var params = {
			url: 'plateform/order/takeout/refund_query',
			data: {
				id: order.id,
			},
			success: function(message) {
				util.$toast(message);
			}
		};
		util.jspost(params);
	} else {
		var params = {
			url: 'plateform/order/takeout/status',
			data: {
				id: order.id,
				type: type
			},
			confirm: tip,
			success: function(message) {
				util.$toast(message);
				if(options.from == 'detail' && success_status) {
					options.vue.order.status = success_status;
					options.vue.order = Object.assign({}, options.vue.order);
				} else {
					if(type == 'notify_deliveryer_collect' && options.filter.items.status == 3) {
						return;
					}
					var types = ['handle', 're_notify_deliveryer_collect', 'end', 'notify_deliveryer_collect'];
					if(types.indexOf(type) != -1 && options.index >= 0) {
						if(options.from == 'search') {
							options.vue.orders.data[options.index].status = success_status;
						} else {
							options.vue.orders.data.splice(options.index, 1);
						}
					}
				}
			}
		};
		util.jspost(params);
	}
};

function onPushUU(options) {
	if(!options.orderId) {
		return false;
	}
	setData({
		vue: options.vue,
		url: 'plateform/order/takeout/push_uupaotui',
		data: {
			id: options.orderId
		},
		success: function(res) {
			setData({
				vue: options.vue,
				url: 'plateform/order/takeout/push_uupaotui',
				confirm: res.tips,
				data: {
					id: res.id,
					push: 1
				},
				success: function(res) {
					options.vue.util.$toast(res);
				}
			});
		},
	});
};
export {
	getOrderList,
	orderStatusChangeTips,
	changeOrderStatus,
	onPushUU
}



