import util from '@/config/util.js'
import router from '@/router'
function getPaotuiOrderList(params) {
	if(params.force) {
		params.vue.orders = {
			page: 1,
			psize: 15,
			loading: true,
			finished: false,
			empty: false,
			data:[]
		};
	}
	if(params.vue.orders.finished) {
		return false;
	}
	params.vue.util.request({
		url: 'plateform/errander/order/list',
		data: {
			page: params.vue.orders.page,
			psize: params.vue.orders.psize,
			filter: params.data.filter,
			menufooter: params.data.menufooter,
		}
	}).then((res) => {
		let result = res.data.message;
		params.vue.showPreLoading = false;
		if(result.errno) {
			params.vue.util.$toast(result.message);
			return false;
		}
		var order = result.message.order;
		params.vue.orders.data = [...params.vue.orders.data,...order];
		if(order.length < params.vue.orders.psize ) {
			params.vue.orders.finished = true;
			if(!params.vue.orders.data.length) {
				params.vue.orders.empty = true;
			}
		}
		params.vue.orders.page++;
		params.vue.orders.loading = false;
		params.vue.isRefresh = false;
		params.vue.menufooter = window.menufooter;
	})
};

function paotuiOrderStatusChangeTips(type) {
	var tips = {
		end: {
			tip: '确定完成订单吗',
			success_status: 3
		},
		cancel: {
			tip: '确定取消订单吗',
			success_status: 4
		},
		cancel_refund: {
			tip: '确定取消订单并退款吗',
			success_status: 4
		},
		refund_handle: {
			tip: '确定发起退款吗',
			success_status: 2
		},
		refund_status: {
			tip: '确定设置为已退款吗',
			success_status: 3
		},
		refund_query: '',
	}
	return tips[type];
};

function changePaotuiOrderStatus(options) {
	var order = options.order;
	var type = options.type;
	var typeInfo = paotuiOrderStatusChangeTips(type);
	var tip = typeInfo.tip;
	var success_status = typeInfo.success_status;
	if(type == 'cancel' || type == 'cancel_refund') {
		var params = {
			url: 'plateform/errander/order/cancel?id=' + order.id,
			confirm: tip,
			success: function(message) {
				util.$toast(message);
				if(options.from == 'detail') {
					options.vue.order.status = success_status;
				} else if(options.from == 'search') {
					options.vue.orders.data[options.index].status = success_status;
				} else if(options.index >= 0) {
					options.vue.orders.data.splice(options.index, 1);
				}
			}
		};
		util.jspost(params);
	} else if(type == 'refund_handle') {
		var params = {
			url: 'plateform/errander/order/refund_handle?id=' + order.id,
			confirm: tip,
			success: function(message) {
				util.$toast(message);
				if(options.from == 'detail') {
					options.vue.order.refund_status = success_status;
				} else {
					options.vue.orders.data[options.index].refund_status = success_status;
					if(options.vue.filter.items.status != 4 && options.from != 'search') {
						options.vue.orders.data.splice(options.index, 1);
					}
				}
			}
		};
		util.jspost(params);
	} else if(type == 'refund_status') {
		var params = {
			url: 'plateform/errander/order/refund_status?id=' + order.id,
			confirm: tip,
			success: function(message) {
				util.$toast(message);
				if(options.from == 'detail') {
					options.vue.order.refund_status = success_status;
				} else {
					options.vue.orders.data[options.index].refund_status = success_status;
				}
			}
		};
		util.jspost(params);
	} else if(type == 'refund_query') {
		var params = {
			url: 'plateform/errander/order/refund_query?id=' + order.id,
			success: function(message) {
				util.$toast(message);
			}
		};
		util.jspost(params);
	} else {
		var params = {
			url: 'plateform/errander/order/status?id=' + order.id + '&type=' + type,
			confirm: tip,
			success: function(message) {
				util.$toast(message);
				var types = ['end'];
				if(options.from == 'detail') {
					options.vue.order.status = success_status;
					options.vue.order = Object.assign({}, options.vue.order);
				} else if(types.indexOf(type) != -1 && options.index >= 0) {
					if(options.from == 'search') {
						options.vue.orders.data[options.index].status = success_status;
					} else {
						options.vue.orders.data.splice(options.index, 1);
					}
				}
			}
		};
		util.jspost(params);
	}
};
export {
	getPaotuiOrderList,
	paotuiOrderStatusChangeTips,
	changePaotuiOrderStatus
}



